.banner {
    /* background: linear-gradient(rgb(0, 102, 255,0.1),rgb(0, 102, 255,1) 95%); */
    background: linear-gradient(rgb(0, 102, 255,0.1), rgb(0, 0, 0,1) 95%),url(/static/media/backg.d74791cb.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    color: white;
    height: 68vw;
    padding: 16vw 4vw;
    /* padding-top: 21vw; */
    min-height: 325px;
    max-height: 950px;
    background-position: center;
}

.banner h2 {
    /* font-size: 5vw; */
    margin-top: clamp(20px,10vw,30px);
    font-size: clamp(30px,5vw,70px);
}
.banner h3 {
    /* font-size: 3.5vw; */
    /* font-size: 12px; */
    font-size: clamp(18px,3.5vw,48px);
    color: rgb(0, 178, 263);
