  }



.plans{
    padding: 0 5vw;
}

.plan-cards {
    display: grid;
    grid-template-columns: auto auto auto;
    padding: 20px 0;
}

.plan-card {
    /* height: 220px; */
    background-color: #36454f;
    color: white;
    box-shadow: 0px 0px 10px 1px var(--primary);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 10px 0;
    border-radius: 7px;
    margin: 20px auto;
    width: 75%;
    cursor: pointer;
    transition: .7s;

}

.plan-card:hover {
    background-color: rgb(0, 178, 263);
    box-shadow: 0 5px 10px 5px rgb(0, 178, 263, 0.502);
}

.plan-card:hover hr {
    border-color: rgb(0, 178, 263);
}

.plan-card button {
    width: 50%;
    margin: 0 auto;
    cursor: pointer;
}
.plan-card p {
    /* border-bottom: 1px solid var(--primary); */
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 10px auto;
    font-size: var(--card-p-font);
    line-height: var(--card-p-line);
    /* color: grey; */
}

.plan-card hr {
    width: 90%;
    margin: 0 auto;
    border-color: var(--primary);
}

.why-choose {
    background-color: grey;
    padding: 30px 3vw;
    margin-bottom: 40px;
}

.why-choose h1 {
   font-size: var(--h2-font);
}

.why-choose h2 {
    color: var(--primary);
    display: flex;
    justify-content: center;
    grid-gap: 20px;
    gap: 20px;
}

.why-choose p {
    font-size: var(--p-font);
    line-height: var(--p-line);
    margin-top: 10px;
}

.choose-cards {
    display: flex;
    flex-wrap: wrap;
}

.choose-card {
      /* height: 220px; */
      background-color: rgb(0, 0, 0);
      color: white;
      padding: 20px 10px;
      border-radius: 7px;
      margin: 20px auto;
      width: 45%;
      cursor: pointer;
      transition: .7s;
}

.choose-card img {
    height: 30px;
}

.testimony-cards {
    display: flex;
    overflow-x: scroll;
    grid-gap: 10%;
    gap: 10%;
    scroll-behavior: smooth;
    scroll-snap-type: x mandatory;
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
    z-index: 2;
}

.testimony-cards::-webkit-scrollbar {
    display: none;
  }

.testimony-card {
    background-color: var(--secondary);
    padding: 20px;
    border-radius: 6px;
    height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    text-align: left;
    position: relative;
    min-width: 100%;
    scroll-snap-align: start;
}

.testimony-card img {
    height: 70px;
    width: 70px;
    border-radius: 50%;
    border: 2px solid rgb(0,230,263);
    position: absolute;
    top: 48%;
    right: 8%;
}

.testimony-card hr {
    border: 1px solid rgb(0,230,263);
    position: absolute;
    top: 59%;
    width: 86%;
}

.team-cards {
    display: flex;
    justify-content: space-around;
}

.member {
    background-color: #36454f;
    padding: 20px 0;
    border-radius: 8px;
    width: 40%;
    max-width: 500px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    transition: .7s;
}

.member:hover {
    background-color: rgb(0, 178, 263);
    box-shadow: 0 5px 10px 5px rgb(0, 178, 263, 0.502);
}

.member img {
    width: 70%;
}

@media (max-width: 800px) {
    .plan-cards {
        grid-template-columns: auto auto;
    }


}

.plan-card {
    width: 90%;
}

@media (max-width: 550px) {

    .banner {
        height: 400px;
    }

    .banner h2 {
        margin-top: 50px;
        margin-bottom: 30px;
    }

    .banner p {
        display: none;
    }

    .choose-card {
        width: 95%;
